.table-container {
    overflow-x: auto;
}

.text-grey {
    color: $secondaryText;
}

.link {
    color: $primary;
    text-decoration: none;
}

table {
    width: 100%;
    &.stripped {
        thead {
            background-color: #fbfbfb;
        }
        tbody {
            background-color: #fff;
            tr {
                &:nth-child(2n) {
                    background-color: #fbfbfb;
                }
                &:last-child {
                    border-bottom-left-radius: 4px;
                    border-bottom-right-radius: 4px;
                }
            }
        }
        th {
            font-weight: 500;
            color: $primaryText;
            border: 1px solid #e5e5e5;
            &:first-child {
                border-left: none;
            }
            &:last-child {
                border-right: none;
            }
        }
        td {
            color: $primaryText;
            border-right: 1px solid #e5e5e5;
            border-left: 1px solid #e5e5e5;
            &:first-child {
                border-left: none;
            }
            &:last-child {
                border-right: none;
            }
        }
    }
    &.multitable {
        margin-bottom: 30px;
        &:last-child {
            margin-bottom: 0;
        }
        tr.darken {
            td,
            th {
                border-right: 1px solid #d3d3d3;
                border-top: none;
                border-bottom: none;
                &:first-child {
                    border-left: none;
                }
                &:last-child {
                    border-right: none;
                }
            }
        }
        td.bold.big,
        td.with-button {
            border: none;
        }
        td.pd-big {
            border-top: 1px solid #d3d3d3 !important;
        }
        tr {
            td,
            th {
                border: 1px solid #d3d3d3;
                &:first-child {
                    border-left: none;
                }
                &:last-child {
                    border-right: none;
                }
            }
        }
    }
}

tr {
    &.darken {
        td,
        th {
            color: $primaryText;
            background-color: #e5e5e5;
        }
    }
    &.pd-sm {
        td,
        th {
            padding: 15px 32px;
        }
    }
}

th,
td {
    text-align: left;
    vertical-align: middle;
    &.bold {
        font-weight: 500;
        color: $primaryText;
    }
    &.big {
        font-size: 21px;
    }
    &.bigger {
        font-size: 18px;
    }
    &.pd-big {
        padding: 30px 32px;
    }
}

th {
    padding: 25px 20px;
    box-sizing: border-box;
    &.text-align-cener {
        text-align: center;
        padding: 0 8px;
    }
}

td {
    padding: 18px 20px;
    box-sizing: border-box;
    &.text-align-cener {
        text-align: center;
        padding: 0 8px;
    }
    &.danger {
        color: $danger !important;
    }
}

.caption {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    box-sizing: border-box;
    width: 100%;
    padding: 20px 32px;
    text-align: left;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    background-color: #fff;
    margin-top: 16px;
    &-title {
        flex: 1 1 auto;
        font-size: 24px;
        font-weight: 500;
        color: $primaryText;
    }
    .btn {
        margin: 0 6px;
    }
    .btn-group {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
    }
    @media (max-width: 700px) {
        flex-direction: column;
        &-title {
            text-align: center;
        }
        .btn-group {
            flex-direction: column;
            margin: 10px 0;
        }
        .btn {
            margin: 10px 0;
        }
    }
}

.table-cost {
    &-caption {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding: 25px 0;
        &-title {
            font-size: 20px;
            font-weight: 500;
            margin-right: 30px;
        }
    }
    &-badges {
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-direction: column;
        width: 100%;
        margin: 26px 0;
        .badge {
            margin: 8px 0;
        }
    }
}
