.badge {
    display: inline-block;
    padding: 6px 9px;
    color: $badgeText;
    background-color: $badge;
    border-radius: 4px;
    strong {
        color: $primaryText;
        font-weight: 500;
    }
}

.breadcrumb {
    padding: 18px 0;
    border-bottom: 1px solid #e5e5e5;
    margin-bottom: 18px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    &-item {
        position: relative;
        display: inline-block;
        padding: 0 10px;
        padding-left: 22px;
        white-space: nowrap;
        margin: 5px 0;
        &-link {
            text-decoration: none;
            color: $secondaryText;
        }
        &.active {
            color: $primaryText;
            font-weight: 500;
        }
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 50%;
            width: 12px;
            height: 1px;
            background-color: #c7c7c7;
            transform: translateY(-50%);
        }
        &:first-child {
            padding-left: 0;
            &::before {
                display: none;
            }
        }
    }
}

.header {
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 75px;
    padding: 0 18px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-bottom: 1px solid #e5e5e5;
    @media (max-width: 1285px) {
        justify-content: space-between;
    }

    .badge {
        display: none;
        @media (max-width: 1285px) {
            display: inline-block;
            position: absolute;
            top: 16px;
            right: 16px;
        }
    }

    &-avatar {
        display: none;
        @media (max-width: 1285px) {
            overflow: hidden;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 70px;
            height: 70px;
            border-radius: 50%;
            margin: 0 auto;
            img {
                width: 100%;
            }
        }
    }

    &-menu {
        &-btn {
            display: none;
        }
        @media (max-width: 1285px) {
            display: none;
            position: fixed;
            top: 80px;
            right: 5px;
            padding-top: 38px;
            padding-bottom: 70px;
            background-color: #fff;
            border-radius: 6px;
            box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
            &-btn {
                flex: 0 0 auto;
                justify-content: flex-end;
                position: relative;
                display: block;
                padding: 0;
                width: 30px;
                height: 20px;
                outline: none;
                border: none;
                background-color: transparent;
                cursor: pointer;
                &-line,
                &::before,
                &::after {
                    width: 100%;
                    height: 2px;
                    background-color: $primaryText;
                    border-radius: 2px;
                    transition: transform 0.1s linear, top 0.1s linear, bottom 0.1s linear, opacity 0.1s linear;
                }
                &::before,
                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                }
                &::before {
                    top: 0;
                }
                &::after {
                    bottom: 0;
                }
                &.active {
                    .header-menu-btn-line {
                        opacity: 0;
                    }
                    &::before {
                        top: 50%;
                        transform: translateY(-50%) rotate(45deg);
                    }
                    &::after {
                        bottom: 50%;
                        transform: translateY(50%) rotate(-45deg);
                    }
                }
            }
            &.active {
                display: block;
            }
        }
        @media (min-width: 1286px) {
            flex: 1 1 auto;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &-link {
        transition: color 0.3s linear;
        &:hover {
            color: $primaryText;
        }
    }

    &-logo {
        max-width: 230px;
        &-link {
            display: flex;
            align-items: center;
            justify-content: center;
            text-decoration: none;
            color: $primaryText;
            font-size: 12px;
            font-weight: 500;
        }
        &-img {
            width: 30px;
            height: 30px;
            margin-right: 10px;
        }
    }

    &-home {
        flex: 1 1 auto;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        text-decoration: none;
        color: $secondaryText;
        margin-left: 40px;
        margin-right: 70px;
        white-space: nowrap;
        &-img {
            margin-right: 14px;
        }
        @media (max-width: 1285px) {
            display: none;
        }
    }

    &-controls {
        @media (max-width: 1285px) {
            display: block;
            &-item-link-img {
                display: none;
            }
            &-notification {
                z-index: 3;
                position: absolute;
                top: 16px;
                left: 16px;
                .notification {
                    &-wrapper {
                        position: absolute;
                        top: 100%;
                        left: -10px;
                        right: auto;
                        padding-top: 20px;
                    }
                    &-triangle {
                        left: 20px;
                        right: auto;
                    }
                }
            }
            &-profile {
                margin-bottom: 38px;
                text-align: center;
                &-name {
                    margin-top: 22px;
                    color: $primaryText;
                    font-size: 20px;
                    font-weight: 500;
                    line-height: 23px;
                    margin-bottom: 6px;
                }
                &-position {
                    color: $secondaryText;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 16px;
                }
                a {
                    text-decoration: none;
                }
            }
            &-disability {
                position: absolute;
                bottom: 16px;
                left: 16px;
                height: 36px;
                button {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
            }
            &-lang {
                display: flex;
                position: absolute;
                bottom: 16px;
                right: 16px;
                .btn + .btn {
                    margin: 0 5px;
                }
                .btn:last-child {
                    margin-right: 0;
                }
            }
        }
        @media (min-width: 1286px) {
            display: flex;
            align-items: stretch;
            justify-content: center;
            &-profile {
                &-name,
                &-position {
                    display: none;
                }
            }
            &-item {
                display: flex;
                align-items: stretch;
                justify-content: center;
                padding: 2px 36px;
                border-right: 1px solid #e5e5e5;
                white-space: nowrap;
                &:last-child {
                    border-right: none;
                }
                &-link {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    text-decoration: none;
                    color: $secondaryText;
                    &-img {
                        margin-left: 16px;
                        padding: 7px;
                        border: 2px solid #e5e5e5;
                        border-radius: 4px;
                        transition: background-color 0.3s linear;
                        &:hover {
                            background-color: #e5e5e5;
                        }
                    }
                }
                .btn + .btn {
                    margin: 0 5px;
                }
            }
        }
    }
}

.disability {
    &-controls {
        &-color {
            .btn {
                &.rotate {
                    transform: rotate(180deg);
                }
                img {
                    transform: rotate(0deg);
                }
            }
        }
    }
}

.aside {
    z-index: 9;
    position: fixed;
    top: 75px;
    left: 0;
    bottom: 0;
    width: 250px;
    background-color: #fff;
    @media (max-width: 350px) {
        width: 290px !important;
    }
    @media (max-width: 1285px) {
        position: initial;
        width: 300px;
        .btn {
            display: none;
        }
        &-nav {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            &-item {
                position: relative;
                width: 100%;
                transition: background-color 0.3s linear;
                &-link {
                    width: 100%;
                    box-sizing: border-box;
                    display: block;
                    color: $badgeText;
                    text-decoration: none;
                    padding: 18px 20px;
                }
                &::after {
                    content: "";
                    width: 9px;
                    height: 16px;
                    position: absolute;
                    top: 50%;
                    right: 20px;
                    background-position: center center;
                    background-repeat: no-repeat;
                    background-size: contain;
                    transform: translateY(-50%);
                    background-image: url("data:image/svg+xml,%3Csvg width='8' height='13' viewBox='0 0 8 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L7 6.5L1 12' stroke='%23797979' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                }
                &:hover {
                    background-color: rgba($color: $primary, $alpha: 0.1);
                }
                &.active {
                    background-color: $primary;
                    a {
                        color: #fff;
                    }
                    &:hover {
                        background-color: darken($color: $primary, $amount: 10%);
                    }
                    &::after {
                        background-image: url("data:image/svg+xml,%3Csvg width='8' height='13' viewBox='0 0 8 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L7 6.5L1 12' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                    }
                }
            }
        }
    }
    @media (min-width: 1286px) {
        transform: translateX(-100%);
        &.active {
            transform: translateX(0);
            .btn {
                left: auto;
                right: 0;
                img {
                    transform: rotate(0deg);
                }
            }
        }
        .btn {
            position: fixed;
            left: 100%;
            top: 50%;
            transform: translateY(-50%);
            img {
                transform: rotate(180deg);
            }
        }
        &-nav {
            width: 100%;
            &-item {
                width: 100%;
                &-link {
                    width: 100%;
                    padding: 18px;
                    display: inline-block;
                    box-sizing: border-box;
                    color: $badgeText;
                    font-size: 14px;
                    text-decoration: none;
                    transition: background-color 0.3s linear;
                    &:hover {
                        background-color: rgba($color: $primary, $alpha: 0.1);
                    }
                }
                &.active {
                    .aside-nav-item-link {
                        color: #fff;
                        background-color: $primary;
                        &:hover {
                            background-color: darken($color: $primary, $amount: 10%);
                        }
                    }
                }
            }
        }
    }
}

.container {
    padding-top: 75px;
    padding-left: 75px;
    padding-right: 38px;
    padding-bottom: 38px;
    &.aside-open {
        padding-left: 290px;
    }
    @media (max-width: 1285px) {
        padding-left: 38px !important;
    }
    @media (max-width: 700px) {
        padding-left: 20px !important;
        padding-right: 20px !important;
    }
}

.filter {
    &.inline {
        display: flex;
    }
    &-title {
        color: $primaryText;
        font-weight: 500;
        font-size: 20px;
    }
    &-form {
        display: flex;
        align-items: flex-start;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin-top: 6px;
        .input-group {
            margin: 12px;
            margin-left: 0;
        }
        .input-multiple-group {
            margin: 12px;
            margin-left: 0;
        }
    }
}

.modal {
    z-index: 15;
    position: fixed;
    display: flex;
    display: none;
    align-items: center;
    justify-content: center;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(22px);
    &.active {
        display: flex;
    }
    &-content {
        box-sizing: border-box;
        overflow-y: auto;
        width: 550px;
        max-height: 100%;
        background-color: #fff;
        border-radius: 4px;
        padding: 39px 26px 26px 32px;
        &-title {
            font-weight: 500;
            font-size: 28px;
        }
        &-footer {
            display: flex;
            justify-content: flex-end;
            margin-top: 25px;
            width: 100%;
            .btn {
                margin: 0 6px;
                &:first-child {
                    margin-left: 0;
                }
                &:last-child {
                    margin-right: 0;
                }
            }
        }
        &.modal-member-checking {
            form input {
                margin-top: 30px;
            }
            .member-check {
                margin-bottom: 0;
                padding-bottom: 25px;
                border-bottom: 1px solid #e5e5e5;
                &:last-child {
                    border-bottom-color: transparent;
                }
            }
        }
        form {
            width: 100%;
            display: flex;
            align-items: stretch;
            justify-content: center;
            flex-direction: column;
            margin-top: 37px;
            margin-bottom: 34px;
            input {
                margin: 6px 0;
                &:first-child {
                    margin-top: 0;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        @media (max-width: 550px) {
            width: 100%;
            height: 100%;
        }
    }
}

.member-check {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: 7px;
    margin-bottom: 19px;
    padding: 0 2px;
    &.disabled {
        display: none;
    }
    &-item {
        &-title {
            position: relative;
            display: inline-block;
            color: $tertiaryText;
            font-size: 14px;
            margin-bottom: 5px;
            padding-right: 23px;
            &::after {
                content: "";
                position: absolute;
                right: 0;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: contain;
            }
        }
        &-value {
            color: $primaryText;
            font-size: 14px;
        }
        &.correct {
            .member-check-item-title::after {
                width: 20px;
                height: 14px;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.5303 2.96967C14.8232 3.26256 14.8232 3.73744 14.5303 4.03033L6.28033 12.2803C5.98744 12.5732 5.51256 12.5732 5.21967 12.2803L1.46967 8.53033C1.17678 8.23744 1.17678 7.76256 1.46967 7.46967C1.76256 7.17678 2.23744 7.17678 2.53033 7.46967L5.75 10.6893L13.4697 2.96967C13.7626 2.67678 14.2374 2.67678 14.5303 2.96967Z' fill='%232F80ED'/%3E%3C/svg%3E%0A");
            }
        }
        &.error {
            .member-check-item-title::after {
                width: 14px;
                height: 14px;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.76855 0.86201C4.89358 0.736986 5.06315 0.666748 5.23996 0.666748H10.76C10.9368 0.666748 11.1063 0.736986 11.2314 0.86201L15.138 4.76868C15.2631 4.8937 15.3333 5.06327 15.3333 5.24008V10.7601C15.3333 10.9369 15.2631 11.1065 15.138 11.2315L11.2314 15.1382C11.1063 15.2632 10.9368 15.3334 10.76 15.3334H5.23996C5.06315 15.3334 4.89358 15.2632 4.76855 15.1382L0.861888 11.2315C0.736864 11.1065 0.666626 10.9369 0.666626 10.7601V5.24008C0.666626 5.06327 0.736864 4.8937 0.861888 4.76868L4.76855 0.86201ZM5.5161 2.00008L1.99996 5.51622V10.4839L5.5161 14.0001H10.4838L14 10.4839V5.51622L10.4838 2.00008H5.5161Z' fill='%23EB5757'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.4714 5.52851C10.7318 5.78886 10.7318 6.21097 10.4714 6.47132L6.47145 10.4713C6.2111 10.7317 5.78899 10.7317 5.52864 10.4713C5.26829 10.211 5.26829 9.78886 5.52864 9.52851L9.52864 5.52851C9.78899 5.26816 10.2111 5.26816 10.4714 5.52851Z' fill='%23EB5757'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.52864 5.52851C5.78899 5.26816 6.2111 5.26816 6.47145 5.52851L10.4714 9.52851C10.7318 9.78886 10.7318 10.211 10.4714 10.4713C10.2111 10.7317 9.78899 10.7317 9.52864 10.4713L5.52864 6.47132C5.26829 6.21097 5.26829 5.78886 5.52864 5.52851Z' fill='%23EB5757'/%3E%3C/svg%3E%0A");
            }
        }
        &.loading {
            .member-check-item-title::after {
                width: 14px;
                height: 14px;
                border-radius: 50%;
                border: 1px solid #c4c4c4;
                border-bottom: none;
                border-right: none;
                animation: spin 0.3s linear infinite;
                @keyframes spin {
                    from {
                        transform: rotate(0deg);
                    }
                    to {
                        transform: rotate(360deg);
                    }
                }
            }
        }
    }
    &-modal-elem {
        width: 100%;
        display: flex;
        flex-direction: column;
        padding-bottom: 25px;
        padding-top: 29px;
        border-bottom: 1px solid#E5E5E5;
    }
    &-temp-data {
        font-size: 21px;
        color: $primaryText;
        margin: 24px 0;
        &.disabled {
            display: none;
        }
    }
}

.section {
    background-color: #fff;
    border-radius: 4px;
    margin-top: 7px;
    &-header {
        padding: 10px 21px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        border-bottom: 1px solid #e5e5e5;
        * {
            margin: 10px 0;
        }
        &-title {
            color: $primaryText;
            font-size: 23px;
            font-weight: 500;
            margin: 10px;
            margin-right: auto;
            text-align: center;
        }
    }
    &-body {
        &-container {
            margin: 0 32px;
            padding: 15px 0;
            @media (max-width: 700px) {
                margin: 0 16px;
            }
            &.bottom-border {
                border-bottom: 1px solid #e5e5e5;
            }
            &.padding-y-big {
                padding: 36px 0;
            }
        }
    }
}

h1.heading {
    font-size: 23px;
    font-weight: 500;
    color: $primaryText;
    margin: 22px 0;
}

h2.heading {
    font-size: 20px;
    font-weight: 500;
    color: $primaryText;
    margin: 16px 0;
}

.members-list {
    &-member {
        position: relative;
        display: inline-block;
        padding: 15px;
        padding-right: 50px;
        margin: 6px;
        border-radius: 4px;
        border: 1px solid #e5e5e5;
        &-name {
            color: $primaryText;
            margin-bottom: 4px;
        }
        &-position {
            font-size: 14px;
            color: $tertiaryText;
        }
        &-status {
            position: absolute;
            top: 16px;
            right: 16px;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: contain;
            &.success {
                width: 22px;
                height: 16px;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M14.5303 2.96967C14.8232 3.26256 14.8232 3.73744 14.5303 4.03033L6.28033 12.2803C5.98744 12.5732 5.51256 12.5732 5.21967 12.2803L1.46967 8.53033C1.17678 8.23744 1.17678 7.76256 1.46967 7.46967C1.76256 7.17678 2.23744 7.17678 2.53033 7.46967L5.75 10.6893L13.4697 2.96967C13.7626 2.67678 14.2374 2.67678 14.5303 2.96967Z' fill='%232F80ED'/%3E%3C/svg%3E%0A");
            }
            &.error {
                width: 16px;
                height: 16px;
                background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.76855 0.86201C4.89358 0.736986 5.06315 0.666748 5.23996 0.666748H10.76C10.9368 0.666748 11.1063 0.736986 11.2314 0.86201L15.138 4.76868C15.2631 4.8937 15.3333 5.06327 15.3333 5.24008V10.7601C15.3333 10.9369 15.2631 11.1065 15.138 11.2315L11.2314 15.1382C11.1063 15.2632 10.9368 15.3334 10.76 15.3334H5.23996C5.06315 15.3334 4.89358 15.2632 4.76855 15.1382L0.861888 11.2315C0.736864 11.1065 0.666626 10.9369 0.666626 10.7601V5.24008C0.666626 5.06327 0.736864 4.8937 0.861888 4.76868L4.76855 0.86201ZM5.5161 2.00008L1.99996 5.51622V10.4839L5.5161 14.0001H10.4838L14 10.4839V5.51622L10.4838 2.00008H5.5161Z' fill='%23EB5757'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M10.4714 5.52851C10.7318 5.78886 10.7318 6.21097 10.4714 6.47132L6.47145 10.4713C6.2111 10.7317 5.78899 10.7317 5.52864 10.4713C5.26829 10.211 5.26829 9.78886 5.52864 9.52851L9.52864 5.52851C9.78899 5.26816 10.2111 5.26816 10.4714 5.52851Z' fill='%23EB5757'/%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.52864 5.52851C5.78899 5.26816 6.2111 5.26816 6.47145 5.52851L10.4714 9.52851C10.7318 9.78886 10.7318 10.211 10.4714 10.4713C10.2111 10.7317 9.78899 10.7317 9.52864 10.4713L5.52864 6.47132C5.26829 6.21097 5.26829 5.78886 5.52864 5.52851Z' fill='%23EB5757'/%3E%3C/svg%3E%0A");
            }
        }
        &-tooltip {
            z-index: 2;
            &-wrapper {
                position: absolute;
                left: 50%;
                top: 100%;
                padding-top: 10px;
                display: none;
                transform: translateX(-50%);
            }
            &-triangle {
                position: absolute;
                top: 1px;
                left: 50%;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 9px 10px 9px;
                border-color: transparent transparent #fff transparent;
                transform: translateX(-50%);
            }
            &-content {
                padding: 16px 21px;
                background-color: #ffffff;
                border-radius: 4px;
                box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
                .member-check {
                    flex-wrap: wrap;
                    margin: 0;
                }
                .member-check-item {
                    margin-bottom: 10px;
                    &:last-child {
                        margin-bottom: 0;
                    }
                }
                .member-check-item-title,
                .member-check-item-value {
                    white-space: nowrap;
                }
            }
        }
        &:hover {
            border-color: $primary;
            .members-list-member-tooltip-wrapper {
                display: block;
            }
        }
    }
}

.drop-down {
    box-sizing: border-box;
    outline: none;
    padding: 0;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    font-family: $font-family;
    border-radius: 4px;
    white-space: nowrap;
    text-decoration: none;
    transition: background-color 0.3s linear, color 0.3s linear, border-color 0.3s linear;
    width: 100%;
    font-family: $font-family;
    &-text {
        user-select: none;
        cursor: pointer;
        position: relative;
        display: flex;
        align-items: center;
        padding: 13px 15px;
        padding-right: 0;
        font-size: 14px;
        color: $tertiaryText;
        text-align: left;
        &::after {
            content: "";
            user-select: none;
            pointer-events: none;
            position: absolute;
            height: 9px;
            width: 16px;
            right: 18px;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("data:image/svg+xml,%3Csvg width='13' height='8' viewBox='0 0 13 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 1L6.5 7L1 1' stroke='%23797979' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
            transition: transform 0.1s linear;
        }
    }
    &-content {
        display: none;
    }
    &.active {
        .drop-down {
            &-text::after {
                transform: rotate(180deg);
            }
            &-content {
                display: block;
            }
        }
    }
}

.pagination {
    display: flex;
    margin-top: 23px;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    @media (max-width: 700px) {
        flex-direction: column;
        justify-content: center;
        .more {
            margin-right: 0 !important;
            margin-bottom: 20px !important;
        }
    }
    .more {
        margin-right: 46px;
        margin-bottom: 10px;
    }
    &-list {
        display: flex;
        margin-bottom: 10px;
    }
    &-elem {
        color: $primaryText;
        background-color: transparent;
        text-decoration: none;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        transition: background-color 0.3s linear, color 0.3s linear;
        margin: 0 5px;
        &:first-child {
            margin-left: 0;
        }
        &:last-child {
            margin-right: 0;
        }
        &:hover {
            background-color: rgba($color: $primary, $alpha: 0.1);
        }
        &.active {
            background-color: $primary;
            color: #fff;
        }
    }
}

.notification {
    &:hover {
        .notification-wrapper {
            display: block;
        }
    }
    &-wrapper {
        position: absolute;
        top: 100%;
        right: -15px;
        padding-top: 20px;
        display: none;
    }
    &-triangle {
        position: absolute;
        top: 10px;
        right: 25px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 9px 10px 9px;
        border-color: transparent transparent #fff transparent;
    }
    &-content {
        padding: 0 16px;
        padding-top: 19px;
        padding-bottom: 6px;
        border-radius: 4px;
        background-color: #fff;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
        cursor: default;
        min-width: 300px;
        @media (max-width: 400px) {
            min-width: 250px;
        }
        &-title {
            font-size: 18px;
            color: $primaryText;
            margin-bottom: 10px;
            text-align: left;
        }
        &-item {
            cursor: pointer;
            padding: 13px 0;
            border-bottom: 1px solid #e5e5e5;
            text-align: left;
            text-decoration: none;
            display: block;
            &:last-child {
                border-bottom: none;
            }
            &-title {
                font-size: 16px;
                color: $primaryText;
                &.redcircle {
                    &::after {
                        content: "";
                        display: inline-block;
                        width: 8px;
                        height: 8px;
                        background-color: #eb5757;
                        margin-left: 8px;
                        margin-bottom: 1px;
                        border-radius: 50%;
                    }
                }
            }
            &-description {
                font-size: 14px;
                color: $tertiaryText;
                margin-top: 4px;
            }
        }
    }
}
