@import "_vars.scss";
@import "_reset.scss";
@import "_forms.scss";
@import "_components.scss";
@import "_tables.scss";

html {
    height: 100%;
}

body {
    font-family: $font-family;
    width: 100%;
    min-height: 100%;
    background: linear-gradient(0deg, #f7f8f9, #f7f8f9), linear-gradient(0deg, #f7f7f7, #f7f7f7), #f7f8ff;
    &.black-white {
        filter: grayscale(100%);
    }
}
