.btn {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 13px 20px;
    outline: none;
    cursor: pointer;
    color: $primaryText;
    background-color: transparent;
    border: 2px solid transparent;
    font-family: $font-family;
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    border-radius: 4px;
    white-space: nowrap;
    text-decoration: none;
    transition: background-color 0.3s linear, color 0.3s linear, border-color 0.3s linear;
    &:hover {
        background-color: rgba($color: $primary, $alpha: 0.1);
    }
    &.back {
        position: relative;
        color: $secondaryText;
        padding-left: 40px;
        &:hover {
            background-color: rgba($color: $secondaryText, $alpha: 0.1);
        }
        &::before {
            position: absolute;
            left: 18px;
            top: 50%;
            content: "";
            width: 9px;
            height: 16px;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("data:image/svg+xml,%3Csvg width='8' height='12' viewBox='0 0 8 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 11.5L1 6L7 0.5' stroke='%23797979' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
            transform: translateY(-50%);
        }
    }
    &.plus {
        position: relative;
        color: $primary;
        padding: 0;
        padding-left: 35px;
        &::before {
            position: absolute;
            left: 0;
            top: 50%;
            content: "";
            width: 24px;
            height: 24px;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19 3H5C3.89543 3 3 3.89543 3 5V19C3 20.1046 3.89543 21 5 21H19C20.1046 21 21 20.1046 21 19V5C21 3.89543 20.1046 3 19 3Z' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12 8V16' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8 12H16' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
            transform: translateY(-50%);
        }
        &:hover {
            background-color: transparent;
        }
        &.mt-negative {
            margin-top: -15px;
            margin-bottom: 34px;
        }
    }
    &.primary {
        color: #fff;
        background-color: $primary;
        border-color: $primary;
        &:hover {
            border-color: darken($color: $primary, $amount: 10%);
            background-color: darken($color: $primary, $amount: 10%);
        }
        &:disabled {
            cursor: not-allowed;
            background-color: rgba($color: $primary, $alpha: 0.5);
            border-color: transparent;
        }
    }
    &.success {
        color: #fff;
        background-color: $success;
        border-color: $success;
        &:hover {
            border-color: darken($color: $success, $amount: 10%);
            background-color: darken($color: $success, $amount: 10%);
        }
    }
    &.outline {
        color: $primaryText;
        background-color: transparent;
        border-color: rgba($color: $primary, $alpha: 0.2);
        &:hover {
            background-color: rgba($color: $primary, $alpha: 0.2);
            border-color: transparent;
        }
        &.grey {
            border-color: #e5e5e5;
            &:hover {
                background-color: #e5e5e5;
                border-color: transparent;
            }
        }
        &.primary {
            border-color: $primary;
        }
    }
    &.sm {
        padding: 8px 13px;
    }
    &-group {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-wrap: wrap;
        &.align-right {
            justify-content: flex-end;
        }
        &.big-margin {
            .btn {
                margin: 8px;
            }
        }
    }
    &.disability {
        position: relative;
        &:hover {
            .disability-wrapper {
                display: block;
            }
        }
    }
    &.notification {
        padding: 7px;
    }
    &.has-notification {
        &::after {
            content: "";
            position: absolute;
            width: 8px;
            height: 8px;
            top: -4px;
            right: -4px;
            border-radius: 50%;
            background-color: #eb5757;
        }
    }
}

.disability {
    &-wrapper {
        position: absolute;
        right: -15px;
        top: 100%;
        padding-top: 20px;
        display: none;
        @media (max-width: 1280px) {
            right: auto;
            left: -15px;
        }
    }
    &-triangle {
        position: absolute;
        top: 10px;
        right: 30px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 9px 10px 9px;
        border-color: transparent transparent #fff transparent;
        @media (max-width: 1280px) {
            right: auto;
            left: 30px;
        }
    }
    &-controls {
        display: flex;
        align-items: stretch;
        justify-content: center;
        background: #ffffff;
        border-radius: 4px;
        padding: 15px 20px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        &-color {
            border-right: 1px solid #e5e5e5;
            padding-right: 5px;
        }
        &-font {
            display: flex;
            align-items: stretch;
            justify-content: center;
            &-sm,
            &-md,
            &-lg {
                display: flex;
                align-items: center;
                justify-content: center;
            }
            &-sm {
                font-size: 14px;
                margin-left: 5px;
            }
            &-md {
                font-size: 20px;
            }
            &-lg {
                font-size: 26px;
            }
        }
    }
}

::placeholder {
    color: $tertiaryText;
    opacity: 1;
}
:-ms-input-placeholder {
    color: $tertiaryText;
}
::-ms-input-placeholder {
    color: $tertiaryText;
}

input[type="text"] {
    margin: 0;
    padding: 13px 15px;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    outline: none;
    font-family: $font-family;
    color: $secondaryText;
    font-size: 14px;
    transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
    &:hover {
        &:disabled,
        &:checked {
            border-color: #e5e5e5;
        }
        border-color: #b3d7ff;
    }
    &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
    }
    &:disabled {
        cursor: default;
        border-color: #e5e5e5;
        background-color: #e5e5e5;
    }
}

input[type="search"] {
    &::-webkit-search-cancel-button {
        appearance: none;
        -webkit-appearance: none;
        -moz-appearance: none;
    }

    margin: 0;
    padding: 13px 15px;
    padding-right: 42px;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    outline: none;
    font-family: $font-family;
    color: $secondaryText;
    font-size: 14px;
    transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
    &:hover {
        &:disabled,
        &:checked {
            border-color: #e5e5e5;
        }
        border-color: #b3d7ff;
    }
    &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
    }
    &:disabled {
        cursor: default;
        border-color: #e5e5e5;
        background-color: #e5e5e5;
    }
}

.search-wrapper {
    display: inline-block;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        width: 22px;
        height: 23px;
        top: 50%;
        right: 10px;
        user-select: none;
        pointer-events: none;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("data:image/svg+xml,%3Csvg width='22' height='23' viewBox='0 0 22 23' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M10.0833 18.2902C14.1334 18.2902 17.4167 15.007 17.4167 10.9569C17.4167 6.90678 14.1334 3.62354 10.0833 3.62354C6.03325 3.62354 2.75 6.90678 2.75 10.9569C2.75 15.007 6.03325 18.2902 10.0833 18.2902Z' stroke='%23302F33' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M19.25 20.1237L15.2625 16.1362' stroke='%23302F33' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
        transform: translateY(-53%);
    }
}

input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    margin: 0;
    border: none;
    outline: none;
    display: inline-flex;
    align-items: center;
    user-select: none;
    cursor: pointer;
    &::before {
        content: "";
        display: inline-block;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        flex-grow: 0;
        border: 2px solid #e5e5e5;
        border-radius: 4px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 50%;
        transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
    }
    &:checked::before {
        border-color: $primary;
        background-color: $primary;
        background-image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.18182L4.52941 8L11 1' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    }
    &:hover {
        &:disabled,
        &:checked {
            &::before {
                border-color: #e5e5e5;
            }
        }
        &::before {
            border-color: #b3d7ff;
        }
    }
    &:active {
        &:disabled::before {
            border-color: #e5e5e5;
            background-color: transparent;
        }
        &::before {
            background-color: #b3d7ff;
            border-color: #b3d7ff;
        }
    }
    &:focus::before {
        box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
    }
    &:focus {
        &:checked::before {
            border-color: #b3d7ff;
        }
        &::before {
            border-color: #80bdff;
        }
    }
    &:disabled::before {
        cursor: default;
        border-color: #e5e5e5;
        background-color: #e5e5e5;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='2' viewBox='0 0 10 2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L9 1' stroke='%23302F33' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
    }
    & + label {
        cursor: pointer;
    }
}

input[type="date"],
input[type="time"] {
    margin: 0;
    border: none;
    outline: none;
    &::-webkit-calendar-picker-indicator {
        cursor: pointer;
        width: 16px;
        height: 16px;
    }

    cursor: pointer;
    line-height: 14px;
    font-family: $font-family;
    font-size: 14px;
    color: $primaryText;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    padding: 11px 15px;
    transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
    &:hover {
        &:disabled,
        &:checked {
            border-color: #e5e5e5;
        }
        border-color: #b3d7ff;
    }
    &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
    }
    &:disabled {
        cursor: default;
        border-color: #e5e5e5;
        background-color: #e5e5e5;
    }
}

input[type="file"] {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    outline: none;
    &:hover {
        &:disabled,
        &:checked {
            & + label {
                border-color: #e5e5e5;
            }
        }
        & + label {
            border-color: #b3d7ff;
        }
    }
    &:focus {
        & + label {
            box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
        }
    }
    &:disabled {
        & + label {
            cursor: default;
            border-color: #e5e5e5;
            background-color: #e5e5e5;
        }
    }
    & + label {
        user-select: none;
        position: relative;
        cursor: pointer;
        font-size: 16px;
        font-weight: 500;
        font-family: $font-family;
        color: $primary;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 18px 23px;
        padding-left: 55px;
        border: 1px dashed #e5e5e5;
        border-radius: 4px;
        margin: 6px 0;
        margin-right: 12px;
        transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
        &::before {
            content: "";
            position: absolute;
            width: 24px;
            height: 24px;
            left: 23px;
            top: 50%;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0)'%3E%3Cpath d='M16 16L12 12L8 16' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M12 12V21' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M20.39 18.39C21.3653 17.8583 22.1358 17.0169 22.5798 15.9986C23.0239 14.9804 23.1162 13.8432 22.8422 12.7667C22.5682 11.6901 21.9434 10.7355 21.0666 10.0534C20.1898 9.37137 19.1108 9.00072 18 8.99998H16.74C16.4373 7.82923 15.8731 6.74232 15.0899 5.82098C14.3067 4.89964 13.3248 4.16783 12.2181 3.68059C11.1113 3.19335 9.90851 2.96334 8.70008 3.00787C7.49164 3.05239 6.30903 3.37028 5.24114 3.93765C4.17325 4.50501 3.24787 5.30709 2.53458 6.28357C1.82129 7.26004 1.33865 8.38552 1.12294 9.57538C0.90723 10.7652 0.964065 11.9885 1.28917 13.1532C1.61428 14.318 2.1992 15.3938 2.99996 16.3' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M16 16L12 12L8 16' stroke='%232F80ED' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0'%3E%3Crect width='24' height='24' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
            transform: translateY(-50%);
        }
    }
}

.input-file-attachment {
    position: relative;
    display: inline-block;
    border: 1px solid #e5e5e5;
    padding: 13px 15px;
    padding-right: 42px;
    border-radius: 4px;
    &-name {
        font-size: 16px;
        color: $primaryText;
    }
    &-size {
        font-size: 14px;
        color: $tertiaryText;
        margin-top: 4px;
    }
    &-btn {
        cursor: pointer;
        position: absolute;
        border: none;
        background-color: transparent;
        outline: none;
        top: 12px;
        right: 12px;
        width: 12px;
        height: 12px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L9 9' stroke='%239B9B9B' stroke-linecap='round'/%3E%3Cpath d='M1 9L9 1' stroke='%239B9B9B' stroke-linecap='round'/%3E%3C/svg%3E%0A");
    }
}

select {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    margin: 0;
    border: none;
    outline: none;

    cursor: pointer;
    line-height: 16px;
    font-family: $font-family;
    font-size: 16px;
    color: $primaryText;
    background-color: #fff;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    padding: 13px 15px;
    padding-right: 40px;
    min-width: 150px;
    transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
    &:hover {
        &:disabled,
        &:checked {
            border-color: #e5e5e5;
        }
        border-color: #b3d7ff;
    }
    &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
    }
    &:disabled {
        cursor: default;
        border-color: #e5e5e5;
        background-color: #e5e5e5;
    }
}
.select-wrapper {
    display: inline-block;
    position: relative;
    &::after {
        content: "";
        user-select: none;
        pointer-events: none;
        position: absolute;
        height: 9px;
        width: 16px;
        top: 50%;
        right: 18px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        background-image: url("data:image/svg+xml,%3Csvg width='13' height='8' viewBox='0 0 13 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 1L6.5 7L1 1' stroke='%23797979' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
        transform: translateY(-50%);
    }
}

textarea {
    margin: 0;
    padding: 13px 15px;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    outline: none;
    font-family: $font-family;
    color: $secondaryText;
    font-size: 14px;
    transition: background-color 0.1s linear, border-color 0.1s linear, box-shadow 0.1s linear;
    resize: vertical;
    &:hover {
        &:disabled,
        &:checked {
            border-color: #e5e5e5;
        }
        border-color: #b3d7ff;
    }
    &:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: $primary, $alpha: 0.25);
    }
    &:disabled {
        cursor: default;
        border-color: #e5e5e5;
        background-color: #e5e5e5;
    }
}

.input-group {
    display: flex;
    flex-direction: column;
    margin: 24px 0;
    input,
    select {
        margin: 6px 0;
    }
    select {
        width: 100%;
    }
    label {
        color: $primaryText;
        font-weight: 500;
        font-size: 14px;
        margin-bottom: 13px;
    }
    &-multiple {
        margin: 36px 0;
        &-title {
            font-size: 14px;
            color: $primaryText;
            font-weight: 500;
            margin-bottom: 15px;
        }
        &-item {
            label,
            input,
            select {
                margin: 8px;
            }
            label {
                display: inline-block;
                font-size: 16px;
                color: $primaryText;
                min-width: 200px;
            }
        }
    }
    &-checkbox {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 24px 0;
        input[type="checkbox"] {
            margin-right: 16px;
        }
    }
    &-file {
        display: flex;
        align-items: stretch;
        justify-content: flex-start;
        flex-wrap: wrap;
        margin: 24px 0;
        .input-file-attachment {
            margin: 6px 0;
            margin-right: 12px;
            &:last-child {
                margin-right: 0;
            }
        }
    }
    &-inline {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &-characters-count {
        margin-bottom: 13px;
        font-size: 14px;
        color: $tertiaryText;
        user-select: none;
        pointer-events: none;
    }
}
.input-multiple-group {
    display: flex;
    flex-direction: column;
    margin: 24px 0;
    label {
        color: $primaryText;
        font-weight: 500;
        font-size: 14px;
        margin-bottom: 13px;
    }
    &-wrapper {
        margin: 6px 0;
        display: inline-block;
        border-radius: 4px;
        border: 1px solid #e5e5e5;
        background: #ffffff;
        box-sizing: border-box;
        padding: 10px 15px;
        input[type="text"] {
            margin: 0;
            display: inline-block;
            padding: 3px 15px;
            border: none;
            border-right: 1px solid #e5e5e5;
            border-radius: 0;
            &:first-child {
                padding-left: 0;
            }
            &:last-child {
                padding-right: 0;
                border-right: none;
            }
            &:hover {
                &:disabled,
                &:checked {
                    border-color: #e5e5e5;
                }
                border-color: #e5e5e5;
            }
            &:focus {
                box-shadow: none;
            }
            &:disabled {
                cursor: default;
                border-color: transparent;
                background-color: transparent;
            }
        }
        &:hover {
            border-color: #b3d7ff;
        }
    }
}

input.inline {
    display: inline-block;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    border: none;
    outline: none;
    padding: 0;
    margin: 0;
    &:hover {
        &:disabled,
        &:checked {
            border-color: transparent;
        }
        border-color: transparent;
    }
    &:focus {
        box-shadow: none;
    }
    &:disabled {
        border-color: transparent;
        background-color: transparent;
    }
}
